import React, {useEffect, useState} from 'react';
import {useHistory, useParams} from 'react-router-dom';
import styled from 'styled-components';
import './Detail.scss';

let 박스 = styled.div`
  padding : 20px;
`;

// ` : backtick
// ${} : backtick 안에서 사용할 수 있는 변수선언문법
let 제목 = styled.h4`
  font-size : 25px;
  color : ${props => props.색상}
`;

// Life-Cycle Hook
// class Detail2 extends React.Component {
//   //컴포넌트가 Mount되었을 때 실행
//   componentDidMount(){
    
//   }

//   //컴포넌트가 UnMount(삭제)되었을 때 실행
//   componentWillUnmount(){

//   }
// }


function Detail(props){

    // 컴포넌트가 mount되었을 때, 컴포넌트가 update 될 때 특정 코드 실행
    
    let {id} = useParams();
    let history = useHistory();
    let [이펙트, 이펙트변경] = useState(true);
    let [인풋, 인풋변경] = useState('');
    
    useEffect(()=>{
      // mount
      let 타이머 = setTimeout(()=>{
        이펙트변경(false);
      },2000)

      // unmount. 타이머 제거
      return ()=>{ clearTimeout(타이머)}
      
    },[]); //[조건] => 처음 <Detail>가 랜더링 될 때만 실행. 다른 state 넣을 수 있음.


    // 주소로 넘어온 파라미터로 id 찾기
    /*
    let 찾은상품 = props.mini.find(function(상품) {
        return 상품.id == id;
    });
    */
    let 찾은상품 = props.mini.find((x)=> x.id == id);
    //let [찾은재고,찾은재고변경] = useState(props.재고);
    let [임시, 임시변경] = useState(props.재고[id]);

    return (
    <div className="container">
        <박스>
          <제목 className="red">제품상세</제목>
        </박스>

        <input onChange={(e)=>{
          인풋변경(e.target.value) //인풋에 입력된 값
          }
        }></input>
        
        {인풋}
        {
          이펙트 === true ? <Alert/> : null
        }

        <div className="row">

          <div className="col-md-6">
            <img src={찾은상품.image} width="100%" />
          </div>

          <div className="col-md-6 mt-4">
            <h4 className="pt-5">{찾은상품.title}</h4>
            <p>{찾은상품.content}</p>
            <p>{찾은상품.price}</p>

          
            <Info 재고 = {임시}></Info>
            
            <button className="btn btn-danger" onClick={()=>{
              let 임시재고 = props.재고;
              임시재고[id] = 임시재고[id]-1;
              
              console.log("주문하기 버튼 : " + 임시재고);
              
              props.재고변경(임시재고);
              임시변경(임시재고[id])
              
            }

            }>주문하기</button> 
            &nbsp;
            <button className="btn btn-danger" onClick={()=>{
                history.goBack();
            }}>뒤로가기</button> 
          </div>
      </div> 
    </div>
    );
  }

  function Alert(){
    console.log("alert 진입");

    return (
      <div className="my-alert2">
          <p>재고가 얼마 남지 않았습니다!</p>
        </div>
    );
  }

  function Info(props){
    console.log("Info : " + props.재고)
    return(
      <p>재고 : {props.재고} </p>
      //<p>재고 : {props.재고[0]}</p>
    )
  }

  export default Detail;